@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&family=Lobster&family=Pacifico&display=swap');

/* General styles */
.App {
    text-align: center;
}

body {
    overflow-x: hidden; /* Hide horizontal overflow */
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
}
  
.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}

.nav-link {
    padding: 0.5rem;
    transition: color 0.3s;
    cursor: pointer;
}

.nav-link:hover {
    color: #9CA3AF; /* Tailwind gray-400 */
}

.scrollbar-hide {
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
}

.scrollbar-hide::-webkit-scrollbar {
    display: none;  /* Safari and Chrome */
}

/* Header styles */
header {
    background-color: #232324; /* Update to the desired color */
    color: #fff;
    padding: 1rem 2rem;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
  
ul {
    display: flex;
    justify-content: space-around;
    width: 80%; /* Adjust width to bring links closer together */
    list-style-type: none;
    padding: 0;
    margin: 0 auto;
}
  
ul li {
    margin: 0 4rem;
}
  
ul li:first-child {
    margin-left: 85px; /* Add space to the left of the first link */
}
  
ul li:last-child {
    margin-right: auto; /* Add space to the right of the last link */
}
  
.nav-link {
    display: flex;
    font-size: 1.25rem;
    color: #fff;
    text-decoration: none;
    transition: color 0.3s;
}
  
.nav-link:hover {
    color: #61dafb;
}

/* Footer styles */
footer {
    background-color: #232324; /* Update to the desired color */
    color: #fff;
    padding: 1rem 0;
    text-align: center;
}

/* Welcome section */
.welcome-section {
    padding: 5rem 0; /* Adjust padding to control height */
    margin-top: 60px;
}
  
.welcome-section h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}
  
.welcome-section p {
    font-size: 1.25rem;
    margin-bottom: 5rem;
}

/* Default styles for mobile devices */
@media (min-width: 480px) {
    body {
        font-size: 16px;
    }
}
/* Small screens (tablets, 768px and up) */
@media (min-width: 768px) {
    body {
      font-size: 18px;
    }
}
  
  /* Medium screens (small laptops, 1024px and up) */
@media (min-width: 1024px) {
    body {
      font-size: 10px;
    }
}
  
  /* Large screens (desktops, 1280px and up) */
@media (min-width: 1280px) {
    body {
      font-size: 12px;
    }
}
  
  /* Extra large screens (large desktops, 1920px and up) */
@media (min-width: 1920px) {
    body {
      font-size: 14px;
    }
}