/* Project styles */
.projects-container {
    display: flex;
    justify-content: flex-start; /* Align items to the start (left) */
    padding: 0rem 0rem; /* Add padding around the project tiles */
    max-width: 1500px; /* Limit the width of the container */
    height: 180px;
    margin: 50px; /* Center the container */
}
  
.project-tile {
    position: relative;
    flex: 0 0 420px; /* Ensure the width is applied */
    margin: 0 1rem; /* Space between tiles */
    height: 205px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.3s;
    box-shadow: 10px 12px 12px rgba(0, 0, 0, 0.8); /* Add shadow */
}
  
.project-tile:hover {
    background-color: #9ca3af; /* Tailwind gray-400 */
}
  
.project-tile .overlay {
    position: absolute;
    inset: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    transition: opacity 0.3s;
}
  
.project-tile:hover .overlay {
    opacity: 1;
}
  
.project-tile .overlay a {
    display: block;
    margin: 0.5rem 0;
    padding: 0.5rem 1rem;
    border-radius: 0.25rem;
    text-align: center;
    background-color: #333; /* Dark grey background */
    color: white;
    text-decoration: none;
    transition: background-color 0.3s;
}
  
.project-tile .overlay a:hover {
    background-color: #555; /* Lighter grey on hover */
}
  
/* Ensure project images fit within the tiles */
.project-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
}

/* Default styles for mobile devices */
@media (min-width: 480px) {
    body {
        font-size: 16px;
    }
}
/* Small screens (tablets, 768px and up) */
@media (min-width: 768px) {
    body {
      font-size: 18px;
    }
}
  
  /* Medium screens (small laptops, 1024px and up) */
@media (min-width: 1024px) {
    body {
      font-size: 20px;
    }
}
  
  /* Large screens (desktops, 1280px and up) */
@media (min-width: 1280px) {
    body {
      font-size: 22px;
    }
}
  
  /* Extra large screens (large desktops, 1920px and up) */
@media (min-width: 1920px) {
    body {
      font-size: 24px;
    }
}