.resume-image-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.resume-image {
    max-width: 100%;
    height: auto;
    border: 2px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

