/* Waves effect */
#waves-container canvas {
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    overflow: hidden;
    z-index: -1;
}

/* Custom scrollbar styles for webkit browsers */
::-webkit-scrollbar {
  width: 16px; /* Vertical scrollbar width */
  height: 16px; /* Horizontal scrollbar height */
}

::-webkit-scrollbar-track {
  background: #2C2C2C; /* Charcoal grey for the track */
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #2C2C2C, #4A4A4A); /* Two-tone charcoal grey for the thumb */
  border-radius: 8px; /* Round edges */
  border: 3px solid #2C2C2C; /* Adds padding around the thumb */
}

::-webkit-scrollbar-thumb:hover {
  background: #555; /* Lighter grey on hover */
}

/* Custom scrollbar styles for Firefox */
* {
  scrollbar-width: 16px; /* Options: auto, thin, none */
  scrollbar-color: #232324 #313131; /* Thumb color first, track color second */
}

/* About section */
.about-section {
    margin-top: 40px;
    margin-left: 100px;
    padding: 5rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about-container {
    display: flex;
    align-items: flex-start;
    max-width: 800px; /* Adjust width to make it narrower */
    margin: 0 auto;
  }
  
  .about-image {
    width: 260px; /* Adjust width as needed */
    height: 340px; /* Adjust height to create an oval shape */
    border-radius: 50%;
    object-fit: flex; /* Ensure the image covers the entire oval shape */
    margin-right: 4rem;
    margin-left: -100px;
    margin-top: 5px;
    box-shadow: 10px 12px 12px rgba(0, 0, 0, 0.8);
  }
  
  .about-title {
    position: absolute;
    top: -40px; /* Adjust as needed */
    left: -20px; /* Adjust as needed */
    transform: rotate(-15deg);
    font-family: 'Dancing Script', cursive;
    font-size: 28px; /* Adjust as needed */
    color: #333; /* Adjust color as needed */
  }
  
  .about-text {
    max-width: 400px; /* Make the text container narrower */
    text-align: left;
    font-family: 'Dancing Script', cursive; /* Apply Dancing Script font */
  }
  
  .about-text p {
    font-size: 25px;
    text-indent: 2em; /* Adjust the indent size as needed */
  }

/* Default styles for small mobile devices */
@media (min-width: 320px) {
    body {
        font-size: 16px;
    }
}

  /* Default styles for mobile devices */
@media (min-width: 480px) {
    body {
        font-size: 16px;
    }
}

/* Small screens (tablets, 768px and up) */
@media (min-width: 768px) {
    body {
      font-size: 18px;
    }
}
  
  /* Medium screens (small laptops, 1024px and up) */
@media (min-width: 1024px) {
    body {
      font-size: 20px;
    }
}
  
  /* Large screens (desktops, 1280px and up) */
@media (min-width: 1280px) {
    body {
      font-size: 22px;
    }
}
  
  /* Extra large screens (large desktops, 1920px and up) */
@media (min-width: 1920px) {
    body {
      font-size: 24px;
    }
}